<template>
    <transition name="modal">
        <div class="modal-mask" v-show="show">
            <div class="modal-container">
                <div class="modal-dialog" role="document">
                    <div class="modal-content">
                       <a> <i class="fa fa-times close-btn" @click="close"></i></a>

                        <div class="alert-contain">
                            <h5>Order was placed successfully</h5>
                            <p class="text-center text-inside">
                                Thank you for trusting us. kindly check your email for more details of your order
                            </p>
                            


                        </div>
                    </div>
                </div>
            </div>
        </div>
    </transition>

</template>

<script>
    
    export default {
        name: "order-successful-modal",
        props: ['show'],
        components: {  },

        data: function () {
            return {
                 
            }
        },
        mounted: function () {
            document.addEventListener("keydown", (e) => {
                if (this.show && e.keyCode == 27) {
                    this.close();
                }
            });
        },
        methods: {
            close: function () {
                this.$emit('close');
            }
        }
    }
</script>
