<template>
    <div>
     <transition name="modal">
        <div class="modal-mask" v-show="show">
            <div class="modal-container">
                <div class="modal-dialog" role="document">
                    <div class="modal-content">
                        
                        <div class="alert-contain">
                            <p class="text-inside text-center" :caption="caption">
                              {{caption}}
                            </p>
                           
                         <div class="text-center mb-3 mt-4">
                                <button @click="close" style="border-radius: 5px !important;padding: 0 40px !important;"
                                    class="mr-3 btn btn-rounded btn-peexoo text-white" :cancelbtnText="cancelbtnText">{{cancelbtnText}}
                                </button>
                                <button @click="handleSubmit" style="border-radius: 5px !important;padding: 0 30px !important;"
                                    class="btn btn-rounded btn-peexoo text-white" :submitbuttonText="submitbuttonText" >{{submitbuttonText}}
                                </button>
                            </div>
                             <p class="text-center peexo-body-text">
                                Tip: <router-link class="peexo-highlight-text" to="/subscribe"> Subscribe</router-link> for FindmyFace, and download unlimited events
                             </p>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </transition>
</div>
</template>

<script>
    
    export default {
        name: "add-to-collection-modal",
        props: ['show','caption', 'nextlineCaption','cancelbtnText', 'submitbuttonText','to' ],
        components: {   },

        data: function () {
            return {
                 
                
            }
        },
        mounted: function () {
            document.addEventListener("keydown", (e) => {
                if (this.show && e.keyCode == 27) {
                    this.close();
                }
            });
        },
        methods: {
            close: function () {
                this.$emit('close');
            },
            handleSubmit: function(){
                this.$emit('submit-modal');
                 
            }
            
        }
    }
</script>
